:root {
    --color-green: #AAC2AA;
    --color-red: #EC5C4E;
    --color-grey-dark: #111;
    --color-grey: #555;
    --color-off-white: #EEEEEE;
    --color-purple: #9A93BE;

    --color-good: var(--color-green);
    --color-bad: var(--color-red);
    --color-actions: var(--color-purple);
}
.background--good {
    background: var(--color-good);
}

.background--bad {
    background: var(--color-bad);
}

.background--actions {
    background: var(--color-actions);
}

.border-color--good {
    border-color: var(--color-good);
}

.border-color--bad {
    border-color: var(--color-bad);
}

.border-color--actions {
    border-color: var(--color-actions);
}
* {
    box-sizing: border-box;
}

#root {
    height: 100vh;
    overflow: hidden;
}

body {
    font-family: 'Muli', sans-serif;
    line-height: 1.4;
}

button {
    background: black;
    color: white;
    cursor: pointer;
    border: 1px solid black;
    padding: 6px 12px;
    -webkit-transition: background 0.5s ease-in, color 0.5s ease-in;
    transition: background 0.5s ease-in, color 0.5s ease-in;
}

button:hover {
    background: white;
    color: black;
}

